@font-face {
  font-family: "Open Sans";
  src:
    url("./static/fonts/OpenSans-Regular.woff") format("woff"),
    url("./static/fonts/OpenSans-Regular.ttf") format("truetype"),
    url("./static/fonts/OpenSans-Regular.svg#OpenSans-Regular") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Open Sans";
